<template>
  <div>
		<v-card class="rounded-lg">
			<v-card-text>
				{{ title }}
				<v-item-group>
					<v-row no-gutters>
						<template v-for="(timeEl, index) in times">
							<v-col :key="index">
								<v-item v-slot="{ active, toggle }" class="mx-3">
									<v-btn 
										class="white--text mt-2"
										:color="active ? btnsColor[index] : 'second'"
										:disabled="!timeEl.available"
										@click="pressTime(active, timeEl['id']) ? toggle() : ''"
									>
										{{ timeEl.time }}
									</v-btn>
								</v-item>
							</v-col>
							<v-responsive
								v-if="(index + 1) % 2 === 0"
								:key="`Responsive ${index}`"
								width="100%"
							/>
						</template>
					</v-row>
				</v-item-group>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
export default {
	data: () => ({
    btnsColor: ['#8DCF43', '#3EA0D6', '#EB8119', '#7D2880'],
		isSelected: false,
  }),
	props: ['title', 'times', 'select_is_limit'],
	methods: {
		pressTime(active, id) {
			if (active) {
				this.$emit('cancle', this.title)
				this.isSelected = false
				return true
			} 
			else {
				this.$emit('select', this.title, id)

				if (!this.select_is_limit) {
					this.isSelected = true
				}

				return this.isSelected
			}
		},
	}
}
</script>

