<template>
  <div class="mt-10" style="height: calc(100% - 40px);">
		<v-card height="100%" class="mx-15 rounded-lg " color="element_background">
			<v-card-title class="background_color_second white--text">
				<v-row align="center" no-gutters>
					<v-col>
						<h2 style="font-weight: lighter;">Запись в зал</h2>
					</v-col>
				</v-row>
			</v-card-title>


			<template v-if="isLoading">
				<v-card-text class="pa-0" style="height: calc(100% - 64px);">
					<v-container fill-height justify="center">
						<v-row no-gutters>
							<v-col align="center">
								<v-progress-circular
									:size="100"
									:width="8"
									color="primary"
									indeterminate
									class="center-inner"
								/>
							</v-col>
						</v-row>
					</v-container>
				</v-card-text>
			</template>

			<template v-else>
				<template v-if="$store.getters.entryIsOpen">
					<v-card-text>
						<v-row no-gutters>
							<template v-for="(day, index) in availableTimeList">
								<v-col :key="`Entry ${index}`">
									<EntryTimeSelector
										class="mt-3 mx-2"
										:title="weekday_str[day.weekday]"
										:times="day.times"
										:selectIsLimit="selectIsLimit"
										@select="selectTime"
										@cancle="cancleSelect"
									/>
								</v-col>
								<v-responsive
									v-if="(index + 1) % 3 === 0"
									:key="`Responsive ${index}`"
									width="100%"
								/>
							</template>
						</v-row>
					</v-card-text>

					<v-card-actions class="background_color_second" style="position:absolute; bottom:0; width: 100%;">
						<v-spacer/>
						<v-btn color="create_entry_btn" class="white--text" width="250" @click="commitEntries">
							Записаться
						</v-btn>
					</v-card-actions>
				</template>

				<template v-else>
					<v-card-text class="pa-0" style="height: calc(100% - 64px);">
						<v-container fill-height justify="center">
							<v-row no-gutters>
								<v-col align="center">
									<h2>
										Запись в зал закрыта
									</h2>
								</v-col>
							</v-row>
						</v-container>
					</v-card-text>
				</template>
			</template>
		</v-card>
	</div>
</template>

<script>
import {mapGetters} from 'vuex'
import EntryTimeSelector from '@/components/EntryTimeSelector'

export default {
	data: () => ({
		isLoading: true,

    selectedTimes: {},
		selectIsLimit: false,

		weekday_str: ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'],
  }),
	
	computed:{
    ...mapGetters(['availableTimeList']),
  },

	async mounted() {
		this.isLoading = true
		await Promise.all([
			this.$store.dispatch('fetchJsonData', '/entry/check-open'),
			this.$store.dispatch('fetchJsonData', '/avtime/get')
		])
		this.isLoading = false
	},

	components: { EntryTimeSelector },

	methods: {
		selectTime(weekday, id) {
			if (!this.selectIsLimit || Object.keys(this.selectedTimes).includes(weekday)) {
				this.selectedTimes[weekday] = id

				if (Object.keys(this.selectedTimes).length == 3) {
					this.selectIsLimit = true
				}
			}
			else {
				this.$store.commit('showSnackbarError', 'Выбрано максимальное количество дней')
			}
		},

		cancleSelect(weekday) {
			delete this.selectedTimes[weekday]

			if (Object.keys(this.selectedTimes).length < 3) {
				this.selectIsLimit = false
			}
		},

		commitEntries() {
			this.$store.dispatch('pushJsonData', { endpoint: '/entry/create', body: Object.values(this.selectedTimes) })
			this.$store.commit('showSnackbarSuccess', 'Вы успешно записались')
		}
	},
}
</script>

